.home-component {
    .has-search {
        .form-control {
            padding-left: 2.375rem;
            border-radius: 20px;
            background: #f3f3f3;
            font-size: 14px;
            border: 1px solid #f3f3f3;
        }

        .form-control-feedback {
            position: absolute;
            z-index: 2;
            display: block;
            width: 2.375rem;
            height: 2.375rem;
            line-height: 2.375rem;
            text-align: center;
            pointer-events: none;
            color: #000;
            margin-left: 4px;
            font-size: 14px;
        }
    }

    .pokemon-list {
        border: 1px solid #000;
    
        .widget-panel {
            padding: 30px;
            border-radius: 20px;
            color: #ffffff;
            margin-bottom: 6%;
            min-height: 185px;

            h2 {
                font-size: 32px;
            }
        }
        
    }
}